.pictureContainer {
    height: 6rem;
    width: 6rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    border: 1px solid rgba(202, 199, 199, 0.5);
    overflow: hidden;
    margin: 1rem;
}
.picture {
    height: 6rem;
    width: 6rem;
}