@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@500;700;900&display=swap);
* {
  font-family: 'Raleway', sans-serif;
}
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.inputField {
    padding: 0.5rem 0.3rem;
    border: none;
    border-bottom: 2px solid rgba(202, 199, 199, 0.8);
    width: 100%;
    color: rgb(161, 161, 161);
}
.inputField::-webkit-input-placeholder {
    font-size: 1rem;
    color: rgba(128, 128, 128, 0.5);
}
.inputField:-ms-input-placeholder {
    font-size: 1rem;
    color: rgba(128, 128, 128, 0.5);
}
.inputField::placeholder {
    font-size: 1rem;
    color: rgba(128, 128, 128, 0.5);
}
.inputField:focus {
    outline: none;
    border-bottom: 2px solid black;
}

.mainContainer {
    padding-top: 1rem;
}
.basicStudentInfoContainer {
    display: flex;
}
.studentInfoContainer {
    flex: 4 1;
}
.studentInfoContainer h3 {
    margin: 0;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 1.8rem;
}
.studentInfoContainer h4 {
    margin: 0;
    font-weight: 500;
    font-size: 0.8rem;
}
.studentInfo {
    padding: 0.5rem 1rem;
    line-height: 1.2rem;
}
.testGradesTogglerContainer {
    padding: 0 1rem;
}
.testGradesTogglerButton:hover {
    cursor: pointer;
}
.pictureContainer {
    height: 6rem;
    width: 6rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    border: 1px solid rgba(202, 199, 199, 0.5);
    overflow: hidden;
    margin: 1rem;
}
.picture {
    height: 6rem;
    width: 6rem;
}
.testGradesTogglerButton {
    height: 2rem;
    width: 2rem;
    border: none;
    margin: 0 1rem;
}
.plusBackground {
    background: url(data:image/png;base64,) no-repeat center;
}
.minusBackground {
    background: url(data:image/png;base64,) no-repeat center;
}
.searchBoxContainer {
    width: 98%;
    margin: 0.5rem auto;
}
.tagInputDisplayContainer {
    margin-left: 8%; 
}
.studentContainer {
    border-bottom: 1px solid rgba(202, 199, 199, 0.5);
    padding-bottom: 1rem;
}
.tagInputDisplayContainer {
    width: 20rem;
}
.tagInputContainer {
    width: 10rem;
    padding: 0.5rem 0;
}

.tag {
    display: inline-block;
    padding: 0.5rem;
    margin: 0.1rem;
    background-color: rgba(202, 199, 199, 0.5);
    color: black;
    font-size: 0.8rem;
    border-radius: 0.2rem;
}
